.header {
    background-color: rgb(8, 0, 73);
    font-family: Georgia, serif;
    width: 100%;
    height: 165px;
} 
.header a {
    text-decoration: none;
    color: rgb(8, 0, 73);
    padding: 20px;
    font-size: 20px;
 
}
ul {
    display: flex; 
    justify-content: space-between;
    margin-top: 0px;
    padding: 0px;
    width: 100%;
    height: 100px;
    align-items: center;
}
.liHeaderBtn {
    display: flex;
  
}
.liHeaderBtn li {
    margin: 10px;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    list-style: none;
    padding: 10px;
}

.liHeaderSignInfoBtn {
    display: flex;    
}
.liHeaderSignInfoBtn li {
    margin: 10px;
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    list-style: none;
    padding: 10px;
}
.headerLogoLink img{
    height: 260px;
    width: 445px;
    position: absolute;
    top: -85px;
    right: -345px;
    align-items: center;
    z-index: 9999;

}
.headerLogoLink li {
    background: rgba(0, 0, 0, 0);
    position: relative;
}